html {
  color: #2f4f4f;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  line-height: 23px;
}

body {
  margin: 0;
  padding: 30px 35px;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 0 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  background-color: #fff;
  border-radius: 12px;
  width: 200px;
  transition-property: transform;
  transition-duration: 1s;
}

.person:hover {
  z-index: 2;
  background-color: #eee;
  transform: scale(1.2);
}

.photo {
  justify-content: end;
  align-items: center;
  width: 200px;
  display: flex;
}

.photo__img {
  border-radius: 50%;
  width: 200px;
  margin-bottom: 8px;
  transition-property: border-radius;
  transition-duration: 1s;
  position: relative;
}

.photo__img:hover {
  border-radius: 25%;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  margin-right: 10px;
  display: flex;
  position: absolute;
}

.name {
  justify-content: center;
  margin-bottom: 9px;
  font-weight: 700;
  display: flex;
}

.extra {
  color: #000;
  justify-content: center;
  margin-bottom: 19px;
  font-size: 18px;
  font-weight: 500;
  line-height: 21px;
  display: flex;
}
